input:not([type]), input[type="text"], input[type="password"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="month"], input[type="week"], input[type="email"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], input[type="color"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  border-width: 0;
  background-color: #FFFFFF;
  border: 1px solid #cccccc;
  color: rgba(0, 0, 0, 0.75);
  display: block;
  font-size: 20px;
  padding: 0 10px;
  height: 42px;
  line-height: 42px;
  width: 100%;
}

input[type=checkbox]{
  opacity: 0;
  outline: none;
  position: absolute;
  left: -999px;
  margin: 0;
}
input[type=checkbox] + label{
  display: inline-block;
  position: relative;
  top: .25em;
  border: 1px solid #bfcbd9;
  border-radius: 4px;
  box-sizing: border-box;
  width: 22px;
  height: 22px;
  background-color: #fff;
  margin-left: .5em;
  z-index: 1;
  transition: border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
}
input[type=checkbox] + label:before {
  content: '';
  width: 3px;
  height: 12px;
  box-sizing: content-box;
  display: inline-block;
  border: 2px solid #fff;
  border-left: 0;
  border-top: 0;
  position: absolute;
  left: 8px;
  top: 2px;
  transform: rotate(0deg);
  transition: transform .15s cubic-bezier(.71,-.46,.88,.6) .05s;
  transform-origin: center;
}
input[type=checkbox]:checked + label {
  background-color: #20a0ff;
  border-color: #0190fe;
}
input[type=checkbox]:checked + label:before {
  transform: rotate(37deg);
}

